html,
body {
  background: #faf9f5;
  margin: 0;
  padding: 0;
  height: 100%;
}

.preview-container {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.preview-wrapper {
  flex: 1;
  width: 100%;
  overflow-y: auto;
  white-space: normal;
  background: #f4f4f4;
}

.dropdown-item:hover {
  background-color: #f8f9fa;
}

.dropdown-menu {
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}

.dropdown-item {
  padding: 0.5rem 1rem;
  border: none;
  background: none;
  width: 100%;
  text-align: left;
  transition: background-color 0.15s ease-in-out;
}

.dropdown-item:focus {
  background-color: #f8f9fa;
  outline: none;
}

.dropdown-logo {
  width: 20px;
  height: 20px;
  object-fit: contain;
  margin-right: 8px;
  flex-shrink: 0;
}

.form-radio {
  border: 1px solid #6c757d;
  border-radius: 0.375rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  position: relative;
  width: max-content;
}
.form-radio label {
  padding-left: 1.5rem;
  cursor: pointer;
}
.form-radio input {
  position: absolute;
  top: 0.75rem;
  margin: 0;
}

.btn-outline-danger {
  background-color: #f4f4f4;
}

.btn-row {
  padding: 1rem;
  background: white;
  border-radius: 8px;
  display: flex;
  justify-content: end;
  gap: 1rem;
}

@media (max-width: 768px) {
  .btn-row {
    position: relative;
    width: 100%;
    display: grid;
    padding: 0;
    border: none !important;
    margin: 0;
  }
}
.file-upload-zone {
  border: 2px dashed #dee2e6;
  border-radius: 0.375rem;
  background-color: #deffe2;
  cursor: pointer;
  transition: all 0.3s ease;
  border: dashed 2px #dee2e6;
  border-radius: 8px;
}
.file-upload-zone:hover {
  background-color: #f2f2f2;
}
.file-upload-zone.drag-over {
  background-color: #e7f1ff;
  transform: scale(1.02);
}

.sidebar {
  transition: all 0.3s ease-in-out;
}

.sidebar-desktop {
  position: relative;
}
.sidebar-desktop.sidebar-open {
  width: 250px;
  min-width: 250px;
}
.sidebar-desktop.sidebar-closed {
  width: auto;
}

.sidebar-mobile {
  position: fixed;
  top: 56px;
  left: 0;
  z-index: 1050;
  height: calc(100vh - 56px);
}
.sidebar-mobile.sidebar-open {
  transform: translateX(0);
}
.sidebar-mobile.sidebar-closed {
  transform: translateX(-100%);
}

.sidebar-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1040;
}

.transition-margin {
  transition: margin-left 0.3s ease-in-out;
}

.main-with-sidebar {
  margin-left: 0;
}

.main-full-width {
  margin-left: 0;
}